<template>
<div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>商品管理</el-breadcrumb-item>
        <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
        <!-- 添加分类按钮区域 -->
        <el-row>
            <el-button type="primary" @click="addCateButton">添加分类</el-button>
        </el-row>
        <!-- 表格插件区域 -->
        <el-row class="top">
            <tree-table :data="cateList" border :columns="columns" show-index index-text="#" :show-row-hover="false" :expand-type="false" :selection-type="false">
                <!-- 是否有效模板 -->
                <template slot="isok" slot-scope="scope">
                    <i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color: lightgreen"></i>
                    <i class="el-icon-error" v-else style="color: red"></i>
                </template>
                <!-- 排序模板 -->
                <template slot="order" slot-scope="scope">
                    <el-tag size="mini" v-if="scope.row.cat_level === 0">一级</el-tag>
                    <el-tag type="success" size="mini" v-else-if="scope.row.cat_level === 1">二级</el-tag>
                    <el-tag type="warning" size="mini" v-else>三级</el-tag>
                </template>
                <!-- 操作模板 -->
                <template slot="ope" slot-scope="scope">
                    <el-button type="primary" icon="el-icon-edit" size="mini" @click="modifyCate(scope.row)">编辑</el-button>
                    <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteCate(scope.row.cat_id)">删除</el-button>
                </template>
            </tree-table>
        </el-row>
        <!-- 分页功能 -->
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="params.pagenum" :page-sizes="[3, 5, 10, 15]" :page-size="params.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
    </el-card>
    <!-- 添加分类 -->
    <el-dialog title="添加分类" :visible.sync="addCateDialogVisible" width="50%" @close="offCateDialog">
        <!-- 添加分类表单 -->
        <el-form :model="cateForm" :rules="cateFormRules" ref="cateFormRef" label-width="90px">
            <el-form-item label="分类名称:" prop="cat_name">
                <el-input v-model="cateForm.cat_name"></el-input>
            </el-form-item>
            <el-form-item label="父级分类:">
                <!-- 级联选择器 -->
                <el-cascader expandTrigger="hover" v-model="selectedKeys" :options="parentCateList" :props="cascaderProps" @change="parentCateChanged" clearable change-on-select>
                </el-cascader>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="addCateDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="addCateClass">确 定</el-button>
        </span>
    </el-dialog>
    <!-- 修改分类 -->
    <el-dialog title="修改分类" :visible.sync="modifyCateDialogVisible" width="50%">
        <!-- 修改分类表单 -->
        <el-form :model="modifyCateForm" :rules="cateFormRules" ref="modifyCateFormRef" label-width="90px">
            <el-form-item label="分类名称:" prop="cat_name">
                <el-input v-model="modifyCateForm.cat_name"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="modifyCateDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="modifyCateClass">确 定</el-button>
        </span>
    </el-dialog>
</div>
</template>

<script>
export default {
  created() {
    this.getCate()
  },
  data() {
    return {
    // 购物分类数据
      cateList: [],
      // 请求参数值
      params: {
        type: 3,
        // 当前页码
        pagenum: 1,
        // 每页显示多少条数据
        pagesize: 5
      },
      // 数据总数
      total: 0,
      // columns列属性名
      columns: [{
        label: '分类名称',
        prop: 'cat_name'
      },
      {
        // 列标题
        label: '是否有效',
        // 指定类型
        type: 'template',
        // 模板名
        template: 'isok'
      },
      {
        // 列标题
        label: '排序',
        // 指定类型
        type: 'template',
        // 模板名
        template: 'order'
      }, {
        // 列标题
        label: '操作',
        // 指定类型
        type: 'template',
        // 模板名
        template: 'ope'
      }],
      // 添加分类交互显示与隐藏
      addCateDialogVisible: false,
      modifyCateDialogVisible: false,
      // 分类表单数据
      cateForm: {
        // 分类名称
        cat_name: '',
        // 父级id
        cat_pid: 0,
        // 分类等级默认0
        cat_level: 0
      },
      modifyCateForm: {
        // 分类名称
        cat_name: ''
      },
      // 分类表单验证规则
      cateFormRules: {
        cat_name: [{ required: true, message: '请输入分类名称', trigger: 'blur' }]
      },
      // 父级数据列表的数据
      parentCateList: [],
      // 级联选择器props
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 双向绑定选定的值为数组
      selectedKeys: [],
      // 修改分类用的的id
      uid: 0
    }
  },
  methods: {
    //   获取购物分类
    async getCate() {
      const { data: res } = await this.$http.get('categories', { params: this.params })
      if (res.meta.status !== 200) {
        return this.$message.error('页面数据获取失败')
      }
      this.cateList = res.data.result
      // this.pagenum = res.data.pagenum
      this.total = res.data.total
      // this.pagesize = res.data.pagesize
    },
    // 每页个数
    handleSizeChange(sizePage) {
      this.params.pagesize = sizePage
      this.getCate()
    },
    // 当前页
    handleCurrentChange(curPage) {
      this.params.pagenum = curPage
      this.getCate()
    },
    // 添加分类按钮
    addCateButton() {
      this.getCateList()
      this.addCateDialogVisible = true
    },
    // 关闭分类对话框时执行
    offCateDialog() {
      this.$refs.cateFormRef.resetFields()
      this.selectedKeys = []
      this.cateForm.cat_pid = 0
      this.cateForm.cat_level = 0
    },
    // 获取商品分类数据列表
    async getCateList() {
      const { data: res } = await this.$http.get('categories', { params: { type: 2 } })
      if (res.meta.status !== 200) {
        return this.$message.error('分类数据列表请求失败!')
      }
      this.parentCateList = res.data
    },
    // 级联选择器发生变化就触发
    parentCateChanged() {
      if (this.selectedKeys.length > 0) {
        // 选了父级
        this.cateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
        // 默认0级，有一项加一级
        this.cateForm.cat_level = this.selectedKeys.length
        return false
      } else {
        // 没选父级
        this.cateForm.cat_pid = 0
        this.cateForm.cat_level = 0
      }
    },
    // 点击确定
    addCateClass() {
      this.$refs.cateFormRef.validate(async dali => {
        if (!dali) return
        const { data: res } = await this.$http.post('categories', this.cateForm)
        if (res.meta.status !== 200) {
          this.$message.error('添加分类失败!')
        }
        this.$message.success('添加分类成功!')
        this.getCate()
        this.addCateDialogVisible = false
      })
    },
    // 修改分类
    modifyCate(cate) {
      this.modifyCateDialogVisible = true
      this.uid = cate.cat_id
    },
    // 确定修改分类
    async modifyCateClass() {
      const { data: res } = await this.$http.put('categories/' + this.uid, { cat_name: this.modifyCateForm.cat_name })
      if (res.meta.status !== 200) {
        return this.$message.error('修改分类失败!')
      }
      this.$message.success('修改分类成功!')
      this.getCate()
      this.modifyCateDialogVisible = false
    },
    // 删除分类
    async deleteCate(id) {
      const del = await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (del !== 'confirm') {
        return this.$message.info('已经取消删除!')
      }
      const { data: res } = await this.$http.delete('categories/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('删除分类失败!')
      }
      this.$message.success('删除分类成功!')
      this.getCate()
    }
  }
}
</script>

<style lang="less" scoped>
.el-breadcrumb {
    margin-bottom: 15px;
}
.el-cascader {
  width: 100%;
}
.top {
  margin-top: 15px!important;
}
</style>
