<template>
<div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>商品管理</el-breadcrumb-item>
        <el-breadcrumb-item>参数列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
        <!-- 文本提示 -->
        <el-alert title="注意：只允许为第三级分类设置相关参数！" type="warning" :closable="false" show-icon>
        </el-alert>
        <el-row class="margin">
            <el-col>
                <!-- 级联选择器 -->
                <span>选择商品分类：</span>
                <el-cascader v-model="selectedKeys" :options="cateList" :props="cascaderProps" @change="parentCateChanged"></el-cascader>
            </el-col>
        </el-row>
        <!-- tabs栏切换 -->
        <el-tabs v-model="activeName" @tab-click="handleTabClick">
            <!-- 动态 -->
            <el-tab-pane label="动态参数" name="many">
                <el-button type="primary" size="mini" :disabled="isButDisabled" @click="showInput">添加参数</el-button>
                <!-- 表格 -->
                <el-table :data="manyTableDate" stripe style="width: 100%" border>
                    <el-table-column type="expand">
                        <!-- 展开项 -->
                        <template slot-scope="scope">
                            <el-tag closable v-for="(item, i) in scope.row.attr_vals" :key="i" @close="deleteTag(scope.row,i)">{{item}}</el-tag>
                            <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)">
                            </el-input>
                            <el-button v-else class="button-new-tag" size="small" @click="showInputTag(scope.row)">+ New Tag</el-button>
                        </template>
                        <!-- 展开项结束 -->
                    </el-table-column>
                    <el-table-column label="#" type="index">
                    </el-table-column>
                    <el-table-column prop="attr_name" label="参数名称">
                    </el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <el-button type="primary" size="mini" icon="el-icon-edit" @click="modifyParams(scope.row)">修改</el-button>
                            <el-button type="danger" size="mini" icon="el-icon-delete" @click="deleteParams(scope.row)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
            <!-- 静态 -->
            <el-tab-pane label="静态属性" name="only">
                <el-button type="primary" size="mini" :disabled="isButDisabled" @click="showInput">添加属性</el-button>
                <!-- 表格 -->
                <el-table :data="onlyTableDate" stripe style="width: 100%" border>
                    <el-table-column type="expand">
                      <!-- 展开项 -->
                        <template slot-scope="scope">
                            <el-tag closable v-for="(item, i) in scope.row.attr_vals" :key="i" @close="deleteTag(scope.row,i)">{{item}}</el-tag>
                            <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)">
                            </el-input>
                            <el-button v-else class="button-new-tag" size="small" @click="showInputTag(scope.row)">+ New Tag</el-button>
                        </template>
                        <!-- 展开项结束 -->
                    </el-table-column>
                    <el-table-column label="#" type="index">
                    </el-table-column>
                    <el-table-column prop="attr_name" label="参数名称">
                    </el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <el-button type="primary" size="mini" icon="el-icon-edit" @click="modifyParams(scope.row)">修改</el-button>
                            <el-button type="danger" size="mini" icon="el-icon-delete" @click="deleteParams(scope.row)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
        </el-tabs>
        <!-- 添加对话框 -->
        <el-dialog :title="'添加'+dialogTitle" :visible.sync="addDialogVisible" width="50%" @close="editDialogClosed">
            <!-- 添加表单 -->
            <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="90px">
                <el-form-item :label="titleText" prop="attr_name">
                    <el-input v-model="addForm.attr_name"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="addParams">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 修改对话框 -->
        <el-dialog :title="'修改'+dialogTitle" :visible.sync="modifyDialogVisible" width="50%" @close="modifyDialogClosed">
            <!-- 修改表单 -->
            <el-form :model="modifyForm" :rules="modifyFormRules" ref="modifyFormRef" label-width="90px">
                <el-form-item :label="titleText" prop="attr_name">
                    <el-input v-model="modifyForm.attr_name"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="modifyDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="modifyParamsbutt">确 定</el-button>
            </span>
        </el-dialog>
    </el-card>
</div>
</template>

<script>
export default {
  created() {
    this.getCateList()
  },
  data() {
    return {
    // 选中的级联id
      selectedKeys: [],
      //  级联选择器数据
      cateList: [],
      //  级联选择器规则
      cascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      //   tabs栏双向绑定
      activeName: 'many',
      // 动态/静态表格数据
      manyTableDate: [],
      onlyTableDate: [],
      // 对话框的显示与隐藏
      addDialogVisible: false,
      modifyDialogVisible: false,
      // 表单数据
      addForm: {
        attr_name: ''
      },
      // 修改表单数据
      modifyForm: {
        attr_name: ''
      },
      // 表单规则
      addFormRules: {
        attr_name: [{ required: true, message: '请填写参数名称', trigger: 'change' }]
      },
      modifyFormRules: {
        attr_name: [{ required: true, message: '请填写参数名称', trigger: 'change' }]
      },
      // 属性 ID
      attrId: 0
    }
  },
  methods: {
    //   获取数据
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        this.$message.error('获取商品分类失败!')
      }
      this.cateList = res.data
    },
    // 级联选择器回调函数
    parentCateChanged() {
      this.getParamsData()
    },
    // tabs栏回调函数
    handleTabClick() {
      this.getParamsData()
    },
    // 获取参数的列表数据
    async getParamsData() {
      // 证明选中的不是三级分类
      if (this.selectedKeys.length !== 3) {
        this.selectedKeys = []
        this.manyTableDate = []
        this.onlyTableDate = []
        return
      }
      // 证明选中的是三级分类
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, { params: { sel: this.activeName } })
      if (res.meta.status !== 200) {
        return this.$message.error('参数列表请求失败!')
      }
      // 将attr_vals 转换为数组
      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // 添加展开项标签表单的显示与隐藏
        item.inputVisible = false
        // 每一项添加输入数据存放
        item.inputValue = ''
      })
      // 判断是什么类型数据
      if (this.activeName === 'many') {
        this.manyTableDate = res.data
      } else {
        this.onlyTableDate = res.data
      }
    },
    // 添加按钮
    showInput() {
      this.addDialogVisible = true
    },
    // 清空表单
    editDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // 添加参数
    addParams() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post(
          `categories/${this.cateId}/attributes`,
          {
            attr_name: this.addForm.attr_name,
            attr_sel: this.activeName
          }
        )

        if (res.meta.status !== 201) {
          return this.$message.error('添加参数失败！')
        }

        this.$message.success('添加参数成功！')
        this.addDialogVisible = false
        this.getParamsData()
      })
    },
    // 修改
    async modifyParams(attrId) {
      this.modifyDialogVisible = true
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes/${attrId.attr_id}`, {
        params: {
          attr_sel: this.activeName
        }
      })
      this.attrId = attrId.attr_id
      if (res.meta.status !== 200) {
        return this.$message.error('获取；列表信息失败')
      }
      this.modifyForm.attr_name = res.data.attr_name
    },
    // 修改的关闭清空事件
    modifyDialogClosed() {
      this.$refs.modifyFormRef.resetFields()
    },
    // 修改确定提交事件
    modifyParamsbutt() {
      this.$refs.modifyFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.put(
          `categories/${this.cateId}/attributes/${this.attrId}`,
          {
            attr_name: this.modifyForm.attr_name,
            attr_sel: this.activeName
          }
        )

        if (res.meta.status !== 200) {
          return this.$message.error('修改参数失败！')
        }

        this.$message.success('修改参数成功！')
        this.modifyDialogVisible = false
        this.getParamsData()
      })
    },
    // 删除属性事件
    async deleteParams(attr) {
      const isconfirm = await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (isconfirm !== 'confirm') return this.$message.info('已经取消删除')
      const { data: res } = await this.$http.delete(`categories/${this.cateId}/attributes/${attr.attr_id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('属性删除失败！')
      }
      this.$message.success('属性删除成功')
      this.getParamsData()
    },
    // 属性标签 input
    async handleInputConfirm(row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return false
      }
      // 有内容时
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false
      const { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        // 数组转换字符串
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) {
        return this.$message.error('添加属性失败!')
      }
      this.$message.success('添加参属项成功!')
    },
    // // 属性标签 按钮
    showInputTag (row) {
      row.inputVisible = true
      // 获取焦点 自动
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // tag删除时触发
    async deleteTag(row, index) {
      row.attr_vals.splice(index, 1)
      const { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        // 数组转换字符串
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) {
        return this.$message.error('删除标签失败！')
      }
      this.$message.success('删除标签成功！')
    }
  },
  computed: {
    isButDisabled() {
    // 根据级联选择器判断按钮是否可用
      if (this.selectedKeys.length !== 3) {
        return true
      } else {
        return false
      }
    },
    // 选中的三级分类请求id
    cateId() {
      if (this.selectedKeys.length === 3) {
        return this.selectedKeys[2]
      }
      return null
    },
    // 对话框标题
    dialogTitle() {
      if (this.activeName === 'many') {
        return '参数'
      } else {
        return '属性'
      }
    },
    // 动态计算标题的文本
    titleText() {
      if (this.activeName === 'many') {
        return '动态参数'
      }
      return '静态属性'
    }
  }
}
</script>

<style lang="less" scoped>
.el-breadcrumb {
    margin-bottom: 15px;
}
.margin {
    margin: 15px 0;
}
.el-table {
  margin-top: 15px;
}
.el-tag {
  margin: 10px;
}
.el-input {
  width: 120px;
}
</style>
